@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;500;700&display=swap');

:root{

    --light-cyan: hsl(153, 71%, 59%);
    --black: hsl(0, 0%, 8%);
    --dark-grey: #242424; 
    --light-grey: hsl(0, 0%, 85%);
    --white: hsl(0, 0%, 100%);
}

html{
    font-size:62.5%;
    font-family:"Space Grotesk", sans-serif;
    box-sizing: border-box;
}
*{
    margin:0;
    padding:0;
}
.App{

    padding:2rem 2rem;
    background-color: var(--black);
    height:100%;
    width:100vw;
}
main{
    height:100%;
    background-color: var(--black);
    width:111.1rem;
}

/* TEXT SETTINGS */
.black{
    color:var(--black);
}
.white{
    color:var(--white);
}
.light-grey{
    color:var(--light-grey);
    opacity:50%;
}
.dark-grey{
    color:var(--dark-grey);
}
.light-cyan{
    color:var(--light-cyan);
}
.text-center{
  text-align:center;
}
p{
    font-size:1.8rem;
    color:var(--white);
    opacity:80%;
}
.heading-large{
    font-size:8.8rem;
    color:var(--white);
}
.heading-medium{
    font-size:4.8rem;
    color:var(--white);
    text-align: center;
   
}
.heading-small{
    font-size:3.4rem;
    color:var(--white);
    text-align: center;
    opacity:100%;
}

.fs-4{
    font-size:4.8rem;
}
a{
    margin:1rem 0rem;
    color:white;
    text-decoration:none;
    font-size:1.8rem;
    border-bottom:2px solid var(--light-cyan);
    letter-spacing: 3px;
    padding-bottom:1rem;
    text-transform:uppercase;
    font-weight:bold;
}
a:hover{
    color:var(--light-cyan);
}

/* DIV SETTINGS */

.bg-light-grey{
    background-color:var(--light-grey);
}
.bg-dark-grey{
    background-color:var(--dark-grey);
}
.bg-light-cyan{
    background-color:var(--light-cyan);
}
.bg-white{
    background-color: var(--white);
}
.border{
    border:1px solid white;
}
.card{
    padding:4rem 8rem;
    font-weight:bold;
}
/* LAYOUT SETTINGS */

.flex{
    display:flex;
}
.column{
    flex-direction:column;
}
.align-center{
    align-items: center;
}
.justify-right{
    justify-content:right;
}
.justify-center{
  justify-content: center;
}
.space-between{
    justify-content: space-between;
}
.gap-2{
    gap:2rem;
}
.gap-3{
  gap:3rem;
}
.p-2{
    padding:2rem;
}
.p-4{
    padding:4rem;
}
.margin-top-bottom-4{
    margin:4rem 0rem;
}
.margin-top-bottom-2{
  margin:2rem 0rem;
}
/*PAGE SETTINGS */

.main-container{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 
    "headings text";
    width:111.1rem;
    height:34.8rem;
    gap: 2rem;
}
.headings{
    grid-area:headings;
}
.text{
    grid-area:text;
    display: flex;
    flex-direction:column;
    align-items: flex-end;
    gap:2rem;
}

.interactive-container{
    justify-content: space-between;
}

.contact-container{
    height:43.5rem;
    width:54rem;
    display:flex;
  flex-direction:column;
  align-items:center;
  justify-content: center;
  gap:4rem;
  background-color:var(--dark-grey);
}
.form-container{
    height:43.5rem;
    width:54rem;
    display:flex;
  flex-direction:column;
  align-items:center;
  gap:2rem;
  justify-content: center;
    background-color:var(--dark-grey);
}

/* INPUT SETTINGS */

textarea{
    font-family:"Space Grotesk", sans-serif;
    

}

input, textarea{
    width:90%;
    background-color: inherit;
    border:none;
    border-bottom:1px solid white;
    outline:none;
    padding:2rem;
    color:white;
}
input:focus, textarea:focus{
    border-bottom:1px solid var(--light-cyan);
    outline:none;
}

/* OTHER SETTINGS */

.title{
  text-align: center;
  color:var(--white);
  font-weight:bold;
  font-size:3rem;
  opacity:100%;
  margin:2rem 0rem;
}

/* #hero-container{
  background-image: url(./images/pattern-rings.svg);
  background-repeat: no-repeat;
  background-position: 10%;
} */
#hero{
  width:17.8rem;
}
#circle{
  position:absolute;
}

span{
  border-bottom:3px solid var(--light-cyan);
}
hr{
  border-top:1px solid white;
  width:100%;
}
#about-container{
  border-top:1px solid white;
  border-bottom:1px solid white;
  width:100%;
  padding:4rem 0rem;
  margin:4rem 0rem;
}
.projects-container{
  width:100%;
}
.project{
width:38rem;
margin-top:2rem;
margin-bottom:1rem;

}
.project-title{
 opacity:100%;
 font-size:2.5rem;
 font-weight:bold;
 margin:1rem 0rem;
}
.placeholder{
}

@media (min-width:780px){


textarea, input{
        width:60%;
    }

    /* HERO AREA TABLET*/

    nav{
        display:flex;
        justify-content:space-between;
        width:100%;
        align-items:center;
    }
    .justify-left{
        justify-self: left;
    }
    .heading-small{
        font-size:5rem;
    }
    #hero{
        grid-area: image;
        grid-row: 1/ span 3;
        justify-self: flex-end;
        width:24rem;
    }
    .grid-area-text1{
        grid-area:text1;
    }
    .grid-area-text2{
        grid-area:text2;
    }
    .grid-area-text3{
        grid-area:text3;
    }

    /* HERO AREA TABLET */

    /* ABOUT CONTAINER TABLET */

#about-container  div,#about-container h2{
    text-align:left;
}

    /* ABOUT CONTTAINER TABLET */
.placeholder{
    display:flex;
    flex-direction: column;
    align-items: center;
}
.grid-container-projects{
    display:grid;
    grid-template-columns: 1fr 1fr;
}

.justify-end{
    justify-self: flex-end;
}

#hero-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas:
      "text1 image"
      "text2 image"
      "text3 image";
  }
  
.text-align-left-tablet{
    text-align:left;
}

}
/* DESKTOP SETTINGS */
@media (min-width:1560px){
    .App{
        width:70%;
        padding-inline:30rem;
    }

    .justify-self-end{
        justify-self:right;
    }
    #about-container{
        display:grid;
        grid-template-columns: 1fr 1fr 1fr;
    }

    .project{
        width:54rem;
    }

    #contact-container{
        margin-top:10rem;
        display:grid;
        grid-template-columns: 1fr 1fr;
        grid-template-areas: 
        "text input"
        ". send";
        grid-row-gap:2rem;
    }
    .placeholder{
        grid-area:input;
    }
    #send{
        grid-area:send;
    }
}
